import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { func } from 'prop-types';
import { getMovies } from '../services/movieAPI';

function searchByText(listMovies, text) {
  return listMovies.filter((movie) => (
    movie.title.toUpperCase().includes(text.toUpperCase())
      || movie.subtitle.toUpperCase().includes(text.toUpperCase())
      || movie.storyline.toUpperCase().includes(text.toUpperCase())
  ));
}

function search(inputsForm, setMovies) {
  const { text, genre, bookmarked } = inputsForm;
  getMovies().then((movies) => {
    let moviesFiltered = movies;
    switch (genre) {
    case 'action':
      moviesFiltered = moviesFiltered.filter((movie) => movie.genre === 'action');
      if (text) moviesFiltered = searchByText(moviesFiltered, text);
      if (bookmarked) moviesFiltered = moviesFiltered.filter((movie) => movie.bookmarked);
      break;
    case 'comedy':
      moviesFiltered = moviesFiltered.filter((movie) => movie.genre === 'comedy');
      if (text) moviesFiltered = searchByText(moviesFiltered, text);
      if (bookmarked) moviesFiltered = moviesFiltered.filter((movie) => movie.bookmarked);
      break;
    case 'thriller':
      moviesFiltered = moviesFiltered.filter((movie) => movie.genre === 'thriller');
      if (text) moviesFiltered = searchByText(moviesFiltered, text);
      if (bookmarked) moviesFiltered = moviesFiltered.filter((movie) => movie.bookmarked);
      break;
    default:
      if (text) moviesFiltered = searchByText(moviesFiltered, text);
      if (bookmarked) moviesFiltered = moviesFiltered.filter((movie) => movie.bookmarked);
    }
    setMovies(moviesFiltered);
  });
}

function renderInputSearch(inputsForm, setInputsForm) {
  const { text } = inputsForm;
  return (
    <input
      value={ text }
      onChange={ (evt) => setInputsForm({ ...inputsForm, text: evt.target.value }) }
      className="input-search"
      placeholder="Insert text for search"
      name="searchText"
      type="text"
      id="input-searchText"
    />
  );
}

function renderSelectGenre(inputsForm, setInputsForm) {
  const { genre } = inputsForm;
  return (
    <label htmlFor="select-genre" data-testid="select-input-label">
      { 'Genre ' }
      <select
        value={ genre }
        onChange={ (evt) => setInputsForm({ ...inputsForm, genre: evt.target.value }) }
        className="select-search"
        name="selectedGenre"
        id="select-genre"
      >
        <option value="" data-testid="select-option">All</option>
        <option value="action" data-testid="select-option">Action</option>
        <option value="comedy" data-testid="select-option">Comedy</option>
        <option value="thriller" data-testid="select-option">Thriller</option>
      </select>
    </label>
  );
}

function renderRadioFavorite(inputsForm, setInputsForm) {
  const { bookmarked } = inputsForm;
  return (
    <label
      className="label-favorite"
      htmlFor="input-checkbox"
      data-testid="checkbox-input-label"
    >
      <input
        value={ bookmarked }
        onClick={ (evt) => setInputsForm({ ...inputsForm, bookmarked: evt.target.checked }) }
        type="checkbox"
        id="input-checkbox"
        data-testid="checkbox-input"
      />
      {' Favorites'}
    </label>
  );
}

function SearchBar({ setMovies }) {
  const [inputsForm, setInputsForm] = useState({
    text: '', genre: 'All', bookmarked: false,
  });

  useEffect(() => search(inputsForm, setMovies), [inputsForm]);

  return (
    <div className="container-search">
      <Link className="link-search" to="/movies/new">ADD MOVIE</Link>
      <form data-testid="search-bar-form" className="input-container">
        { renderInputSearch(inputsForm, setInputsForm) }
        <div className="container-input-genre-favorite">
          { renderSelectGenre(inputsForm, setInputsForm) }
          { renderRadioFavorite(inputsForm, setInputsForm) }
        </div>
      </form>
    </div>
  );
}

SearchBar.propTypes = {
  setMovies: func.isRequired,
};

export default SearchBar;
