import React, { Component } from 'react';
import { Header, MovieForm } from '../components';
import { getMovie, updateMovie } from '../services/movieAPI';

class EditMovie extends Component {
  constructor(props) {
    super(props);
    this.setEditMovie = this.setEditMovie.bind(this);
    this.state = {
      editMovie: null,
    };
  }

  componentDidMount() {
    const { match } = this.props;
    const id = match.params.id;
    getMovie(id).then((movie) => this.setState({ editMovie: movie }));
  }

  setEditMovie(editMovie) {
    const { history } = this.props;
    updateMovie(editMovie);
    history.push('/movie-cards-library');
  }

  render() {
    const { editMovie } = this.state;

    return (
      <div data-testid="edit-movie">
        <Header />
        {
          (editMovie !== null)
          && <MovieForm type="Edit" movie={ editMovie } setMovie={ this.setEditMovie } />
        }
      </div>
    );
  }
}

export default EditMovie;
