import React, { useState } from 'react';
import { string } from 'prop-types';
import { Link } from 'react-router-dom';

function createTitle(inputsForm, setInputsForm) {
  const { title } = inputsForm;
  return (
    <div>
      <label
        className="label-add-movie"
        htmlFor="input-title"
        data-testid="title-input-label"
      >
        Title
      </label>
      <input
        value={ title }
        onChange={ (evt) => setInputsForm({ ...inputsForm, title: evt.target.value }) }
        className="input-add"
        type="text"
        id="input-title"
        data-testid="title-input"
        name="title"
      />
    </div>
  );
}

function createSubtitle(inputsForm, setInputsForm) {
  const { subtitle } = inputsForm;
  return (
    <div>
      <label
        className="label-add-movie"
        htmlFor="input-subtitle"
        data-testid="subtitle-input-label"
      >
        Subtitle
      </label>
      <input
        value={ subtitle }
        onChange={ (evt) => setInputsForm({ ...inputsForm, subtitle: evt.target.value }) }
        className="input-add"
        type="text"
        id="input-subtitle"
        data-testid="subtitle-input"
        name="subtitle"
      />
    </div>
  );
}

function createImage(inputsForm, setInputsForm) {
  const { imagePath } = inputsForm;
  return (
    <div>
      <label
        className="label-add-movie"
        htmlFor="input-image"
        data-testid="image-input-label"
      >
        Image
      </label>
      <input
        value={ imagePath }
        onChange={ (evt) => setInputsForm({ ...inputsForm, imagePath: evt.target.value }) }
        placeholder="Url image"
        className="input-add image-add"
        type="text"
        id="input-image"
        data-testid="image-input"
        name="imagePath"
      />
    </div>
  );
}

function createStoryline(inputsForm, setInputsForm) {
  const { storyline } = inputsForm;
  return (
    <div className="container-textArea">
      <label
        className="label-add-movie"
        htmlFor="textarea-sinopse"
        data-testid="storyline-input-label"
      >
        StoryLine
      </label>
      <textarea
        value={ storyline }
        onChange={ (evt) => setInputsForm({ ...inputsForm, storyline: evt.target.value }) }
        className="input-add input-textArea"
        id="textarea-sinopse"
        data-testid="storyline-input"
        cols="44"
        rows="5"
        name="storyline"
        maxLength="200"
      >
        {}
      </textarea>
    </div>
  );
}

function createRating(inputsForm, setInputsForm) {
  const { rating } = inputsForm;
  return (
    <div>
      <label
        className="label-add-movie"
        htmlFor="input-rating"
        data-testid="rating-input-label"
      >
        Rating
      </label>
      <input
        value={ rating }
        onChange={ (evt) => setInputsForm({ ...inputsForm, rating: evt.target.value }) }
        className="input-add ratio-add"
        type="number"
        id="input-rating"
        data-testid="rating-input"
        name="rating"
        min="0"
        max="5"
        step="0.1"
      />
    </div>
  );
}

function createFavorite(inputsForm, setInputsForm) {
  const { bookmarked } = inputsForm;
  return (
    <div>
      <label htmlFor="radioIsFavorite" className="label-add-movie">
        <input
          checked={ bookmarked }
          onClick={
            (evt) => setInputsForm({ ...inputsForm, bookmarked: evt.target.checked })
          }
          className="input-add"
          id="radioIsFavorite"
          type="checkbox"
        />
        IsFavorite
      </label>
    </div>
  );

}

function createGenre(inputsForm, setInputsForm) {
  const { genre } = inputsForm;
  return (
    <div>
      <label
        className="label-add-movie"
        htmlFor="input-select"
        data-testid="genre-input-label"
      >
        Genre
      </label>
      <select
        value={ genre }
        onChange={ (evt) => setInputsForm({ ...inputsForm, genre: evt.target.value }) }
        className="input-add genre-add"
        id="input-select"
        data-testid="genre-input"
        name="genre"
      >
        <option value="action" data-testid="genre-option">Action</option>
        <option value="comedy" data-testid="genre-option">Comedy</option>
        <option value="thriller" data-testid="genre-option">Thriller</option>
      </select>
    </div>
  );
}

function validationInputs(inputsForm) {
  const { title, subtitle, imagePath } = inputsForm;
  if (title && subtitle && imagePath) return true;
  return false;
}

function renderAddButton(inputsForm, setMovie) {
  return (
    <div className="container-btn">
      <button
        onClick={ () => {
          if (validationInputs(inputsForm)) setMovie(inputsForm);
          else alert('Invalid information');
        } }
        className="btn-form"
        type="button"
        data-testid="send-button"
      >
        Add Movie
      </button>
    </div>
  );
}

function renderEditButton(inputsForm, setMovie) {
  return (
    <div className="container-btn">
      <button
        onClick={ () => setMovie(inputsForm) }
        className="btn-form"
        type="button"
        data-testid="edit-button"
      >
        Edit Movie
      </button>
    </div>
  );
}

function renderCancelBtton() {
  return (
    <div className="container-btn">
      <Link to="/">
        <button
          className="btn-form"
          type="button"
          data-testid="cancel-button"
        >
          Cancel
        </button>
      </Link>
    </div>
  );
}

function MovieForm({ type, movie, setMovie }) {
  const [inputsForm, setInputsForm] = useState(movie);
  return (
    <div className="container-add-movie">
      <form data-testid="add-movie-form" className="container-form-add">
        <h4 className="subtitle-add-movie">Information about movie</h4>
        <div className="container-inputs titles-form">
          { createTitle(inputsForm, setInputsForm) }
          { createSubtitle(inputsForm, setInputsForm) }
        </div>
        { createImage(inputsForm, setInputsForm) }
        { createStoryline(inputsForm, setInputsForm) }
        { createFavorite(inputsForm, setInputsForm) }
        <div className="container-inputs genre-input">
          { createGenre(inputsForm, setInputsForm) }
          { createRating(inputsForm, setInputsForm) }
        </div>
        <div className="container-btn-form">
          { (type === 'Add') ? renderAddButton(inputsForm, setMovie) : renderEditButton(inputsForm, setMovie) }
          { renderCancelBtton() }
        </div>
      </form>
    </div>
  );
}

MovieForm.propTypes = {
  type: string.isRequired,
};

export default MovieForm;
